.download_wrapp {
  position: relative;
  span {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 200px;
    font-weight: 200;
    color: #fff;
    opacity: 0.8;
    transform: translateY(-70%); }

  h2 {
    position: absolute;
    display: flex;
    flex-direction: row;
    left: 370px;
    transform: translateY(-50%);
    font-size: 72px;
    font-weight: 200;
    font-family: "impact";
    color: #25365d;
    z-index: 2;
    animation: fade-visibility 2s 1s; } }
.download_container {
  display: flex;
  flex-direction: column;
  padding-top: 120px;
  padding-bottom: 145px; }

.download_text {
  span {
    display: block;
    line-height: 25px;
    font-family: "MariadPro";
    letter-spacing: 0.5px;
    height: 55px;
    width: 700px; } }
.download_list {
  font-family: "MariadPro";
  letter-spacing: 0.5px;
  padding-top: 50px;
  line-height: 30px; }
.img_list {
  background-image: url("../img/flag.png");
  background-repeat: no-repeat;
  background-position: left;
  padding-left: 20px;
  a {
    text-decoration: none; } }
.download_button {
  padding-top: 70px; }
.btn_download {
    height: 55px;
    width: 220px;
    border-radius: 25px;
    background-color: #25365d;
    border: 0;
    color: #fff;
    position: relative;
    &:hover {
      cursor: pointer;
      transition: .3s ease-in;
      transform: scale(1.4);
      color: #808080; } }
.download_img {
  position: absolute;
  right: 210px;
  transform: translateX(-90%);
  transform: translateY(-10%);
  animation: fade-visibility 3s 1s; }
